	import slFilter from '@/components/sl-filter/sl-filter.vue';
    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default { 
		data() {
			return {
				list:[],
				loadingText: '正在加载...',
				hasmore:true,
				curpage:1,
				//控制滑动效果
				theIndex:null,
				oldIndex:null,
				isStop:false,
				menuList: [
					{
						'title': '排序',
						'key': 'sort',
						'isSort': true,
						'reflexTitle': true,
						'detailList': [{
								'title': '默认排序',
								'value': ''
							},
							{
								'title': '兑换时间升',
								'value': 'dhasc'
							},
							{
								'title': '兑换时间降',
								'value': 'dhdesc'
							},
							{
								'title': '到期时间升',
								'value': 'dqasc'
							},
							{
								'title': '到期时间降',
								'value': 'dqdesc'
							}
						]
					},
					{
						'title': '状态',
						'key': 'state',
						'isSort': true,
						'reflexTitle': true,
						'detailList': [{
								'title': '全部',
								'value': '0'
							},
							{
								'title': '未使用',
								'value': '1'
							},
							{
								'title': '已使用',
								'value': '2'
							},
							{
								'title': '已失效',
								'value': '3'
							}
						]
					}
				],
				bysort:'',
				state:0
			}				
		},
		components: {slFilter},
        computed: mapState(['hasLogin','userInfo']),  
		onLoad() {
			 console.log('页面加载');
			this.loadData();
		},
         onReachBottom() {
             console.log('下拉加载...');
			 this.curpage++;
			 this.loadData();
         },
         onPageScroll(){
             console.log('页面滚动...');
         },
         onPullDownRefresh() {
             console.log('上拉刷新...');
             uni.stopPullDownRefresh();
         },
        methods: {   
			result(val) {
				console.log(val);
				let _data = JSON.parse(JSON.stringify(val));
				let _keys = Object.keys(_data);
				console.log(_keys);
				if(_keys[0]=='state'){
					if(_data['state']!=this.state){
						this.state=_data['state'];
						this.clearData();
						this.loadData();
					}
				}else if(_keys[0]=='sort'){
					if(_data['sort']!=this.bysort){
						this.bysort=_data['sort'];
						this.clearData();
						this.loadData();
					}
				}
			}, 
			clearData(){
				this.list =[];
				this.loadingText='正在加载...';
				this.hasmore=true;
				this.curpage=1;
			}, 
            async loadData() {  
				var _this = this;
				console.log('===userInfo==='+this.hasLogin);
				console.log(this.userInfo);
				console.log(this.userInfo.token);
                if (!this.hasLogin) {  
					uni.navigateTo({
						url:'login',
					})
                }else{
					if(!this.hasmore){
						this.loadingText = '暂无更多信息';
						return false;
					}
					_this.$Request.post('member_voucher/voucher_list',{curpage:_this.curpage,state:_this.state,bysort:_this.bysort}).then(res => {
						if (res.code == 200) {
							console.log(res);
							_this.hasmore =res.hasmore;
							console.log(_this.hasmore);
							let _list = res.datas.list;
							for(var i=0;i<_list.length;i++){
								_this.list.push(_list[i]);
							}
							if(!_this.hasmore){
								_this.loadingText = '暂无更多信息';
							}
						}
						else{
							uni.showToast({
								icon: 'none',
								title:res.datas.error,
								duration:2000
							});
							uni.navigateTo({
								url:'login',
							});
						}
					});
				}
            },
			// toStore:function(id){
			// 	console.log(id);
			// 	uni.navigateTo({
			// 		url:'pages/store/index?store_id='+id
			// 	})
			// },
			toPage: function(url,id) {
				console.log(url,id);
				uni.navigateTo({
					url:url+id,
				})
			},
			//控制左滑删除效果-begin
			touchStart(index,event){
				console.log('====touchStart====');
				//多点触控不触发
				if(event.touches.length>1){
					this.isStop = true;
					return ;
				}
				this.oldIndex = this.theIndex;
				this.theIndex = null;
				//初始坐标
				this.initXY = [event.touches[0].pageX,event.touches[0].pageY];
			},
			touchMove(index,event){
				console.log('====touchMove====');
				//多点触控不触发
				if(event.touches.length>1){
					this.isStop = true;
					return ;
				}
				let moveX = event.touches[0].pageX - this.initXY[0];
				let moveY = event.touches[0].pageY - this.initXY[1];
				
				if(this.isStop||Math.abs(moveX)<5){
					return ;
				}
				if (Math.abs(moveY) > Math.abs(moveX)){
					// 竖向滑动-不触发左滑效果
					this.isStop = true;
					return;
				}
				
				if(moveX<0){
					this.theIndex = index;
					this.isStop = true;
				}else if(moveX>0){
					if(this.theIndex!=null&&this.oldIndex==this.theIndex){
						this.oldIndex = index;
						this.theIndex = null;
						this.isStop = true;
						setTimeout(()=>{
							this.oldIndex = null;
						},150)
					}
				}
			},
			touchEnd(index,$event){
				console.log('====touchEnd====');
				//结束禁止触发效果
				this.isStop = false;
			},
			//控制左滑删除效果-end
			deleteVoucher(voucher_id){
				var _this = this;
				console.log('====deleteVoucher====');
				_this.$Request.post('member_voucher/voucherdelete',{voucher_id:voucher_id}).then(res => {
					if (res.code == 200) {
						console.log(res);
						let len = _this.list.length;
						for(let i=0;i<len;i++){
							if(voucher_id==_this.list[i].voucher_id){
								_this.list.splice(i, 1);
								break;
							}
						}
						_this.oldIndex = null;
						_this.theIndex = null;
					}
					else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
					}
				});
				
			}
			 
        }  
    }  
